import React from "react"
import { useNavigate } from "react-router-dom"

export default function SeedPhraseWarning() {
    const navigate = useNavigate()

  return (
    <div className="flex min-h-screen flex-col items-center justify-center bg-white p-4">
      <div className="w-full max-w-md flex flex-col items-center px-4">
        <h1 className="text-2xl font-medium text-center mb-8">Просмотр Seed фразы</h1>
        
        <p className="text-center mb-6">
          Для экспорта вашего счета в другое приложение кошелька, вы можете воспользоваться seed фразой
        </p>

        <p className="text-center font-medium mb-12">
          НИКОГДА! не делитесь вашей seed фразой!
          Эта фраза открывает полный доступ к вашим активам.
          Агенты поддержки EXWALLET никогда её не запросят.
        </p>

        <button 
        onClick={() => navigate('/SeedCheckNext')}
        className="w-full max-w-[200px] h-12 text-white font-medium rounded-xl bg-blue-500 hover:bg-blue-600 transition-colors"
        >
          Далее
        </button>
      </div>
    </div>
  )
}