import { generateMnemonic, mnemonicToSeedSync } from "bip39"; // Используем синхронную версию
import HDKey from "hdkey";
import { keccak256 } from "ethereum-cryptography/keccak";
import bs58check from "bs58check";
import { ec as EC } from "elliptic";
// Убедитесь, что crypto-browserify корректно импортирован и поддерживается
import crypto from 'crypto-browserify';

const ec = new EC("secp256k1");

function privateKeyToTronAddress(privateKey) {
  console.log("privateKeyToTronAddress: ", privateKey);  // Логирование приватного ключа
  const key = ec.keyFromPrivate(privateKey);
  const publicKey = key.getPublic().encode("hex").slice(2);
  console.log("publicKey: ", publicKey);  // Логирование публичного ключа
  const addressHash = keccak256(Buffer.from(publicKey, "hex")).slice(-20);
  console.log("addressHash: ", addressHash.toString("hex"));  // Логирование хэша адреса
  const addressHex = Buffer.from(addressHash).toString("hex");
  const tronAddressHex = Buffer.from("41" + addressHex, "hex");
  const tronAddress = bs58check.encode(tronAddressHex);
  console.log("tronAddress: ", tronAddress);  // Логирование финального адреса
  return tronAddress;
}

export async function generateWallet() {
  try {
    console.log("Generating wallet...");
    const mnemonic = generateMnemonic();
    console.log("Mnemonic: ", mnemonic);  // Логирование мнемоники
    const seed = mnemonicToSeedSync(mnemonic);  // Используем синхронную версию
    console.log("Seed: ", seed.toString("hex"));  // Логирование seed

    const root = HDKey.fromMasterSeed(Buffer.from(seed));
    console.log("Root: ", root);  // Логирование root

    const addrnode = root.derive("m/44'/195'/0'/0/0");
    console.log("AddrNode: ", addrnode);  // Логирование addrnode
    const privateKey = addrnode.privateKey.toString("hex");
    console.log("PrivateKey: ", privateKey);  // Логирование приватного ключа

    const address = privateKeyToTronAddress(privateKey);
    console.log("Address: ", address);  // Логирование адреса

    return { mnemonic, privateKey, address };
  } catch (error) {
    console.error("Error generating wallet: ", error);  // Логирование ошибки
  }
}
